import React from "react";
import { Container, Row, Col } from "reactstrap";
import FeatherIcon from "feather-icons-react";
import logolight from "../assets/images/logo-light.png";
import logodark from "../assets/images/logo-dark.png";

const Footer = ({ isLight = true, col1 = [], col2 = [] }) => {
    return (
        <React.Fragment>
            <footer className={isLight ? "footer bg-light" : "footer"}>
                <Container>
                    <Row>
                        <Col lg="4" xs="12" className="mb-0 mb-md-4 pb-0 pb-md-2" name="footercolumn">
                            <a href to="#" className="logo-footer">
                                <img src={isLight ? logodark : logolight} height="24" alt="" />
                            </a>
                            <p className={isLight ? "mt-4 text-muted" : "mt-4"}>
                                Stop puzzling. Start planning.
                            </p>
                            <ul className={isLight ? "list-unstyled social-icon social mb-0 m t-4" : "list-unstyled social-icon foot-social-icon mb-0 mt-4"}>
                                <li className="list-inline-item me-1">
                                    <a href="https://www.linkedin.com/company/dimesoftware" target="_blank" rel="noreferrer" className="rounded">
                                        <FeatherIcon icon="linkedin" className="fea icon-sm fea-social" />
                                    </a>
                                </li>
                                <li className="list-inline-item me-1">
                                    <a href="https://www.youtube.com/channel/UCYD4LGox7SJLL7r8gmjulNA" target="_blank" rel="noreferrer" className="rounded">
                                        <FeatherIcon icon="youtube" className="fea icon-sm fea-social" />
                                    </a>
                                </li>
                                <li className="list-inline-item me-1">
                                    <a href="https://www.facebook.com/dimesoftware" target="_blank" rel="noreferrer" className="rounded">
                                        <FeatherIcon icon="facebook" className="fea icon-sm fea-social" />
                                    </a>
                                </li>
                                <li className="list-inline-item me-1">
                                    <a href="https://twitter.com/dimesoftware" target="_blank" rel="noreferrer" className="rounded">
                                        <FeatherIcon icon="twitter" className="fea icon-sm fea-social" />
                                    </a>
                                </li>
                            </ul>
                        </Col>

                        <Col lg="4" md="4" xs="12" className="mt-4 mt-sm-0 pt-2 pt-sm-0" name="footercolumn">
                            <h5 className={isLight ? "text-dark footer-head" : "text-light footer-head"}>
                                Useful resources
                            </h5>
                            <ul className="list-unstyled footer-list mt-4">
                                {col1.map((grid, key) => (
                                    <li key={key}>
                                        <a href={grid.link} target="_blank" rel="noreferrer" className={isLight ? "text-muted" : "text-foot"}>
                                            <i className="mdi mdi-chevron-right me-1"></i>{" "}
                                            {grid.title}
                                        </a>
                                    </li>
                                ))}
                            </ul>
                        </Col>

                        <Col lg="4" md="4" xs="12" className="mt-4 mt-sm-0 pt-2 pt-sm-0" name="footercolumn">
                            <h5 className={isLight ? "text-dark footer-head" : "text-light footer-head"}>
                                Other
                            </h5>
                            <ul className="list-unstyled footer-list mt-4">
                                {col2.map((grid, key) => (
                                    <li key={key}>
                                        <a href={grid.link} target="_blank" rel="noreferrer" className={isLight ? "text-muted" : "text-foot"}>
                                            <i className="mdi mdi-chevron-right me-1"></i>
                                            {grid.title}
                                        </a>
                                    </li>
                                ))}
                            </ul>
                        </Col>
                    </Row>
                </Container>
            </footer>
        </React.Fragment >
    );
}

export default Footer;
